import React from 'react'
import classNames from 'classnames'

import { 
  Typography,
  Paper,
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'
import { secondCardStyles } from '../ui/Styles'
const useStyles = makeStyles(secondCardStyles)

export default function SecondCard(props) {
  const {
    title,
    description,
    img,
    className,
    imgProps,
    titleProps,
    descriptionProps,
    titleClass,
    descriptionClass,
    ...rest
  } = props
  const classes = useStyles()

  return (
    <Paper
      elevation={0}
      className={classNames(classes.root, className)}
      {...rest}
    >
      <div className={classes.imgContainer} {...imgProps} >
        <img 
          alt='logo'
          src={img}
        />
      </div>
      <Typography 
        className={classNames(classes.title, titleClass)}
        {...titleProps}
      >
        {title}
      </Typography>
      <Typography 
        className={classNames(classes.description, descriptionClass)}
        {...descriptionProps}
      >
        {description}
      </Typography>
    </Paper>
  )
}
