import React, { useState } from 'react'

import ActionButton from './ui/ActionButton'

import RequestDemoForm from './RequestDemoForm'

export default function RequestDemoButton({ className, textClassName, ...rest}) {
  const [open, setOpen] = useState(false)

  const handleClick = () => {
    if(!open) setOpen(true)
  }

  return (
    <React.Fragment>
      <ActionButton
        className={className}
        {...rest}
        onClick={handleClick}
      >
        Request Demo
      </ActionButton>
      <RequestDemoForm open={open} setOpen={setOpen} modal />
    </React.Fragment>
  )
}