import React from 'react'
import classNames from 'classnames'

import * as $ from 'jquery'

import { 
  Modal,
  Paper,
  Typography,
  Input,
  InputLabel,
  IconButton,
} from '@material-ui/core'

import ActionButton from './ui/ActionButton'
import Heading from './Heading'

import cancel from '../assets/images/cancel.svg'

import { makeStyles } from '@material-ui/core/styles'
import { requestDemoStyles } from './ui/Styles'
const useStyles = makeStyles(requestDemoStyles)

function validateEmail(email) {
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}

function sendmail(){
  if($("#name").val() === ""){
    alert("Please enter your Full Name");
    return;
  } else if($("#phone").val() === ""){
    alert("Please enter your Mobile Number");
    return;
  } else if($("#email").val() === ""){
    alert("Please enter your Email Address");
    return;
  } else if (!validateEmail($("#email").val())){
    alert("Please enter a valid Email Address");
    return;
  } else if($("#company").val() === ""){
    alert("Please enter your Company Name");
    return;
  }
  console.log("sending" + $("#sendbutton").html());
  $("#sendbutton").html("Sending");
  $("#sendbutton").prop("disabled", true);
  $("#email").prop("disabled", true);
      $("#name").prop("disabled", true);
      $("#phone").prop("disabled", true);
      $("#company").prop("disabled", true);
      $("#message").prop("disabled", true);
  var json = JSON.stringify({
    email: $("#email").val(),
    name: $("#name").val(),
    mobile: $("#phone").val(),
    company: $("#company").val(),
    message: $("#message").val()
  });
  var xhr = new XMLHttpRequest();
xhr.open("POST", "https://valudot.glitch.me/send-email", true);
xhr.setRequestHeader('Content-Type', 'application/json');
xhr.send(json);
xhr.onload = function() {
    var data = JSON.parse(this.responseText);
    console.log(data.status);
    if(data.status === "sent"){
      $("#sendbutton").html("Sent");
    } else {
      $("#sendbutton").html("Failed! Try Again");
      $("#sendbutton").prop("disabled", false);
      $("#email").prop("disabled", false);
      $("#name").prop("disabled", false);
      $("#phone").prop("disabled", false);
      $("#company").prop("disabled", false);
      $("#message").prop("disabled", false);
    }
};
  console.log('From: ' +$("#email").val()+ '<br>Full Name: ' +$("#name").val()+'<br>Company: '+$("#company").val()+'<br>Mobile: '+$("#phone").val()+'<br>Message: '+$("#message").val());
}

export default function RequestDemoModal(props) {
  const {
    open, 
    setOpen, 
    modal,
    className,
    customTitle,
    ...rest
  } = props
  const classes = useStyles()

  const inputs = [
    {
      label: 'Full Name',
      placeholder: 'Enter your full name',
      type: 'text',
      name: 'name',
      id: 'name',
    },
    {
      label: 'Mobile Number',
      placeholder: 'Enter phone number',
      type: 'number',
      name: 'phone',
      id: 'phone',
    },
    {
      label: 'Email',
      placeholder: 'Enter email address',
      type: 'email',
      name: 'email',
      id: 'email',
    },
    {
      label: 'Company',
      placeholder: 'Enter company name',
      type: 'text',
      name: 'company',
      id: 'company',
    },
  ]

  const component = (
    <Paper
      elevation={0}
      className={classNames(classes.modal, className)}
      {...rest}
    >
      {
        modal ?
          <IconButton
            disableRipple
            style={{
              background: 'transparent',
              position: 'absolute',
              padding: 0,
              right: '4vh',
              top: '4vh',
            }}
            onClick={() => setOpen(false)}
          >
            <img 
              alt='close'
              src={cancel}
            />
          </IconButton> : null
      }
      <form id='forms' action="#" className={classes.modalContent}>
        <Heading
          style={{
            fontSize: '1.5em',
            alignSelf: 'flex-start',
            marginTop: '6vh'
          }}
        >
          {customTitle ? customTitle : 'Request demo'}
        </Heading>
        {inputs.map((input, i) => (
          <div className={classes.inputContainer} >
            <InputLabel 
              className={classes.inputLabel}
              for={input.id}
            >
              {input.label}<span style={{color: '#fe4b5a'}}>*</span>
            </InputLabel>
            <Input
              required
              disableUnderline
              className={classes.input}
              name={input.name}
              id={input.id}
              placeholder={input.placeholder}
            />
          </div>
        ))}
        <div className={classes.inputContainer} >
          <InputLabel 
            className={classes.inputLabel}
            for='message'
          >
            Message
          </InputLabel>
          <Input
            disableUnderline
            className={classes.input}
            style={{
              height: '14.6vh',
              maxHeight: '130px',
            }}
            name='message'
            id='message'
            multiline
            rows={4}
          />
        </div>
        <ActionButton id={'sendbutton'} onClick={()=>sendmail()}
          style={{
            width: '15vh',
            height: '6vh',
            maxWidth: '122px',
            maxHeight: '50px',
            alignSelf: 'flex-end',
            marginTop: '4vh',
          }}
        >
          Submit
        </ActionButton>
      </form>
    </Paper>
  )

  return (
    modal ?
      <Modal
        open={open}
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
        BackdropProps={{
          style: {
            backgroundColor: 'rgba(0, 0, 0, 0.4)',
          }
        }}
      >
        {component}
      </Modal> :
      component
  )
}
