import React from 'react'
import classNames from 'classnames'

import { 
  Typography,
  Paper,
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'
import { thirdCardStyles } from '../ui/Styles'
const useStyles = makeStyles(thirdCardStyles)

export default function SecondCard(props) {
  const {
    title,
    description,
    num,
    className,
    numProps,
    titleProps,
    descriptionProps,
    numClass,
    titleClass,
    descriptionClass,
    ...rest
  } = props
  const classes = useStyles()

  return (
    <Paper
      elevation={0}
      className={classNames(classes.root, className)}
      {...rest}
    >
      <Typography 
        className={classNames(classes.num, numClass)}
        {...numProps}
      >
        {num}
      </Typography>
      <Typography 
        className={classNames(classes.title, titleClass)}
        {...titleProps}
      >
        {title}
      </Typography>
      <Typography 
        className={classNames(classes.description, descriptionClass)}
        {...descriptionProps}
      >
        {description}
      </Typography>
    </Paper>
  )
}
