import React from 'react'
import classNames from 'classnames'

import { 
  Typography,
  Paper,
  Avatar,
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'
import { workflowCardStyles } from '../ui/Styles'
const useStyles = makeStyles(workflowCardStyles)

export default function WorkflowCard(props) {
  const {
    title,
    description,
    img,
    className,
    imgProps,
    avatarProps,
    titleProps,
    descriptionProps,
    titleClass,
    descriptionClass,
    imgClass,
    avatarClass,
    ...rest
  } = props
  const classes = useStyles()

  return (
    <Paper
      elevation={0}
      className={classNames(classes.root, className)}
      {...rest}
    >
      <Avatar 
        className={classNames(classes.avatar, avatarClass)} 
        {...avatarProps}
      >
        <img
          alt='logo'
          src={img}
          className={classNames(classes.img, imgClass)}
          {...imgProps}
        />
      </Avatar>
      <Typography 
        className={classNames(classes.title, titleClass)}
        {...titleProps}
      >
        {title}
      </Typography>
      <Typography 
        className={classNames(classes.description, descriptionClass)}
        {...descriptionProps}
      >
        {description}
      </Typography>
    </Paper>
  )
}
