import React from 'react'
import classNames from 'classnames'

import {
  Typography,
  Avatar,
} from '@material-ui/core'

import linkedIn from '../../assets/images/linked-in.svg'

import Description from '../Description'

import { makeStyles } from '@material-ui/core/styles'
import { TeamMemberStyles } from '../ui/Styles'
const useStyles = makeStyles(TeamMemberStyles)

export default function TeamMember(props) {
  const {
    avatar,
    name,
    title,
    avatarProps,
    nameProps,
    titleProps,
    avatarClass,
    nameClass,
    titleClass,
    className,
    describeClass,
    ...rest
  } = props
  const classes = useStyles()
  
  return (
    <div 
      className={classNames(classes.root, className)}
      {...rest}
    >
      <Avatar 
        src={avatar}
        className={classNames(classes.avatar, avatarClass)}
        {...avatarProps}
      />
      <Typography 
        className={classNames(classes.name, nameClass)}
        {...nameProps}
      >
        {name}
      </Typography>
      <Description 
        className={classNames(classes.title, titleClass)}
        {...titleProps}
      >
        {title}
      </Description>
    </div>
  )
}
