import React from 'react'
import classNames from 'classnames'

import { 
  Typography,
  Avatar,
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'
import { tooltipStyles } from '../ui/Styles'
const useStyles = makeStyles(tooltipStyles)

export default function Tooltips(props) {
  const {
    img,
    name,
    nameProps,
    avatarProps,
    imgProps,
    nameClass,
    avatarClass,
    imgClass,
    className,
    ...rest
  } = props
  const classes = useStyles()

  return (
    <div 
      className={classNames(classes.root, className)}
      {...rest}
    >
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          width:'100%',
          height: '100%',
          borderRadius: '4px',
        }}
      >
        <Avatar 
          className={classNames(classes.avatar, avatarClass)} 
          {...avatarProps}
        >
          <img
            alt='logo'
            src={img}
            className={classNames(classes.img, imgClass)}
            {...imgProps}
          />
        </Avatar>
        <Typography 
          className={classNames(classes.name, nameClass)}
          {...nameProps}
        >
          {name}
        </Typography>
      </div>
    </div>
  )
}
