import React from 'react'

import {
  useTheme,
} from '@material-ui/core/styles'

export default function VertBar({style, className, ...rest}) {
  const theme = useTheme()

  return (
    <div
      style={{
        width: '155px',
        height: '6px',
        borderRadius: '5px',
        transform: 'rotate(180deg)',
        background: `linear-gradient(75deg, ${theme.palette.common.pink} -42%, ${theme.palette.common.orange} 116%)`,
        ...style,
      }}
      className={className}
      {...rest}
    />
  )
}
